'use client'

import { motion, type HTMLMotionProps } from 'framer-motion'
import { Button, type ButtonProps } from '@/components/ui/button'
import { forwardRef } from 'react'

interface AnimatedButtonProps extends ButtonProps {
  whileHover?: HTMLMotionProps<"button">["whileHover"]
  whileTap?: HTMLMotionProps<"button">["whileTap"]
  initial?: HTMLMotionProps<"button">["initial"]
  animate?: HTMLMotionProps<"button">["animate"]
}

export const AnimatedButton = forwardRef<HTMLButtonElement, AnimatedButtonProps>(
  ({ whileHover, whileTap, initial, animate, children, ...props }, ref) => {
    return (
      <motion.div
        whileHover={whileHover || { scale: 1.02 }}
        whileTap={whileTap || { scale: 0.98 }}
        initial={initial}
        animate={animate}
        className="inline-block"
      >
        <Button ref={ref} {...props}>
          {children}
        </Button>
      </motion.div>
    )
  }
)

AnimatedButton.displayName = 'AnimatedButton'

// Pre-configured button animations
export function BounceButton({ children, ...props }: ButtonProps) {
  return (
    <AnimatedButton
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.95 }}
      {...props}
    >
      {children}
    </AnimatedButton>
  )
}

export function PulseButton({ children, ...props }: ButtonProps) {
  return (
    <AnimatedButton
      whileHover={{ scale: 1.02 }}
      whileTap={{ scale: 0.98 }}
      animate={{
        boxShadow: [
          '0 0 0 0 rgba(59, 130, 246, 0.7)',
          '0 0 0 10px rgba(59, 130, 246, 0)',
          '0 0 0 0 rgba(59, 130, 246, 0)',
        ],
      }}
      transition={{
        duration: 2,
        repeat: Infinity,
      }}
      {...props}
    >
      {children}
    </AnimatedButton>
  )
}